@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
  font-family: 'Poppins', sans-serif !important;
}
#home-img {  width: 40%;
  float: left;
  padding-top: 20px;
}

#section-1 {
  width: 100%;
  padding-left: 10%;
  padding-right: 10%;
  height: 100vh;
}

.intro-text {
  width: 60%;
  height: 100%;
  float: left;
  padding-top: 20%;
  padding-left: 100px;
}

#name {
  font-size: 70px;
  font-weight: 700;
}

#text {
  font-size: 30px;
}

#section-2, #section-3, #section-4, #section-5, #section-6 {
  width: 100%;
  padding-left: 10%;
  padding-right: 10%;
  padding-bottom: 70px;
  padding-top: 70px;
  background-color: rgb(237, 237, 237);
  text-align: center;
}

.card-skills, .card-tools {
  width: 50%;
} 

.ant-card-head-title {
  font-size: 30px;
}

.skill {
  font-size: 16px;
}

.skill-icons, .tool-icons {
  justify-content: center;
  text-align: center;
}

.adobe-icon {
  margin-right: 15px;
}

.cando-card {
  text-align: center;
  justify-content: center;
  min-height: 500px;
}
.nav-link {
  height: 5px;
  margin-top: 10px;
  margin-right: 100px;
  font-size: 16px;
  color: #c4c4c4;
  font-weight: 300;
}

.nav-link:after {
  display:block;
  content: '';
  border-bottom: solid 3px rgb(38 38 38);  
  transform: scaleX(0);  
  transition: transform 250ms ease-in-out;
}

.nav-link:not(#resume-btn):hover:after { 
  transform: scaleX(1); 
}

.nav-link:hover { 
  color: rgb(38 38 38);  
}

